import React, { useState } from 'react'

function RSVP (props) {
  const [wish, setWish] = useState({
    name: '',
    relation: '',
    message: '',
  });

  const handleNameChange = (event) => {
    setWish({
      ...wish,
      name: event.target.value
    });
  }

  const handleRelationChange = (event) => {
    setWish({
      ...wish,
      relation: event.target.value
    });
  }

  const handleMessageChange = (event) => {
    setWish({
      ...wish,
      message: event.target.value
    });
  }

  const handleSubmit = (event) => {
    if (props?.sendWish === undefined || props?.sendWish === null) {
      return;
    }
    props?.sendWish(wish);
    event.preventDefault();
  }

  return (
    <div
      id='rsvp'
      className='section-padding-bottom-only'
    >
      <div className='container'>
        <div className='row justify-content-center'>
          <div className='col-md-6 bg-white p-40'>
            {' '}
            <span className='oliven-title-meta text-center'>Kirimkan Ucapan Selamat dan Doa</span>
            <br />
            <form onSubmit={handleSubmit} className='row'>
              <div className='col-md-12'>
                <div className='form-group'>
                  <input
                    type='text'
                    className='form-control'
                    placeholder='Nama'
                    value={wish?.name}
                    onChange={handleNameChange}
                    required
                  />{' '}
                </div>
              </div>
              <div className='col-md-12'>
                <div className='form-group'>
                  <input
                    type='text'
                    className='form-control'
                    placeholder='Hubungan'
                    value={wish?.relation}
                    onChange={handleRelationChange}
                  />{' '}
                </div>
              </div>
              <div className='col-md-12'>
                <div className='form-group'>
                  <textarea
                    name='message'
                    id='message'
                    cols='30'
                    rows='7'
                    className='form-control'
                    placeholder='Ucapan'
                    value={wish?.message}
                    onChange={handleMessageChange}
                    required
                  ></textarea>
                </div>
              </div>
              <div className='col-md-12'>
                <div className='form-group'>
                  <input 
                    type='submit' 
                    className='btn buttono' 
                    value='Kirim' 
                    disabled={wish?.name === '' || wish?.message === ''} 
                  />{' '}
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}

export default RSVP
