import React, { useState, useEffect } from 'react'
import Parse from 'parse/dist/parse.min.js';
import { Helmet } from 'react-helmet';

import Header from './components/Header';
import Countdown from './components/Countdown'
import Sidebar from './components/Sidebar';
import Story from './components/Story';
import RSVP from './components/RSVP';

const PARSE_APPLICATION_ID = 'YS5hRXleZeOALO6kx6SOA1m4M8vxwvkyT3F4dVf8';
const PARSE_HOST_URL = 'https://parseapi.back4app.com/';
const PARSE_JAVASCRIPT_KEY = '8FLBgHI6Ct8nRFRlmeeZF67Utvw5QN34vOb3u8RN';

Parse.initialize(PARSE_APPLICATION_ID, PARSE_JAVASCRIPT_KEY);
Parse.serverURL = PARSE_HOST_URL;

function App() {
  const [wishes, setWishes] = useState([]);

  useEffect(() => {
    fetchWishes();
  }, []);

  const fetchWishes = async () => {
    try {
      const query = new Parse.Query('Wishes');
      const results = await query.find();
      let newArray = [];
      for (let wish of results) {
        newArray.push({
          name: wish.get("name"),
          relation: wish.get("relation"),
          message: wish.get("message"),
        })
      }
      setWishes(newArray);
    } catch (e) {
      console.error(e);
      setWishes([]);
    }
  }
  
  const sendWish = async (e) => {
    try {
      const Wish = new Parse.Object('Wishes');
      Wish.set('name', e?.name ? e?.name : 'Anonymous');
      Wish.set('relation', e?.relation ? e?.relation : '');
      Wish.set('message', e?.message ? e?.message : '');
      await Wish.save();
      alert('Ucapan telah terkirim!');
      setWishes([
        ...wishes,
        e
      ]);
    } catch (error) {
      console.error(error);
    }
  }


  return (
    <>
      <Helmet>
          <title>{ `Puspallia & Shariman Online Wedding Invitation` }</title>
      </Helmet>
      <Sidebar />
      <div id='oliven-main'>
        <Header />
        <Countdown />
        <RSVP sendWish={(e) => sendWish(e)} />
        <Story wishes={wishes} />
      </div>
    </>
  );
}

export default App;
