import React, { useEffect, useState, useMemo } from 'react'

const SECOND = 1000;
const MINUTE = SECOND * 60;
const HOUR = MINUTE * 60;
const DAY = HOUR * 24;

function Countdown () {
  const parsedDeadline = useMemo(() => new Date(1696038300000), []);
  const [time, setTime] = useState(parsedDeadline - Date.now());

  useEffect(() => {
    const interval = setInterval(
        () => setTime(parsedDeadline - Date.now()),
        1000,
    );

    return () => clearInterval(interval);
}, [parsedDeadline]);

  if (time <= 0) {
    return null;
  }

  return (
    <div
      id='countdown'
      className='section-padding'
    >
      <div className='container'>
        <div className='row'>
          <div className='section-head col-md-12'>
            <h4>Akad Nikah akan berlangsung dalam</h4>
          </div>
        </div>
        <div className='row'>
          <div className='col-md-12'>
            <ul>
              <li>
                <span id='days'>{`${Math.floor(time / DAY)}`.padStart(2, "0")}</span>Hari
              </li>
              <li>
                <span id='hours'>{`${Math.floor((time / HOUR) % 24)}`.padStart(2, "0")}</span>Jam
              </li>
              <li>
                <span id='minutes'>{`${Math.floor((time / MINUTE) % 60)}`.padStart(2, "0")}</span>Menit
              </li>
              <li>
                <span id='seconds'>{`${Math.floor((time / SECOND) % 60)}`.padStart(2, "0")}</span>Detik
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Countdown
