import React from 'react'

function Story ({ wishes }) {

  if (wishes?.length === undefined || wishes?.length < 1) {
    return null;
  }

  return (
    <div id='story' className='story section-padding'>
      <div className='container'>
        <div className='row'>
          <div className='col-md-7 animate-box'>
            <h4 className='oliven-story-subtitle'>Ucapan Selamat dan Doa</h4>
            {wishes.map((item, i) => (
              <div className='col-md-12 wishes' key={i}>
                <h2 className='oliven-couple-subtitle'>{`${item?.name} - ${item?.relation}`}</h2>
                <p>
                  {item?.message}
                </p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}

export default Story
